import { Component } from '@angular/core';
import { IonicPage, NavController, ModalController, AlertController, NavParams } from 'ionic-angular';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';


@IonicPage()
@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {
  langus: string = "en";
  quick_list: any;
  langs:any = {};
  isChange: boolean = true;

  constructor(public navCtrl: NavController,
    public alertService: AlertServiceProvider, public alertCtrl: AlertController,
    public modalCtrl: ModalController,public globalVars: GlobalVarsProvider, public navParams: NavParams) {
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() ); 
    
    this.quick_list = [
      { name: this.langs.cheque, image: 'assets/svg/card.svg', link: 'ChequesPage' },
      { name: this.langs.change_pin_txt, image: 'assets/svg/secure.svg', link: 'ChangePinPage' },
      { name: this.langs.enquiries, image: 'assets/svg/enquire.svg', link: 'EnquiriesPage' },
      { name: this.langs.language, image: 'assets/svg/language.svg', link: 'lang' },
      // { name: this.langs.forex, image: 'assets/svg/black/forex.svg', link: 'ForexPage' },
      // { name: 'FAQs', image: 'assets/svg/black/faq.svg', link: 'FaqsPage' },
      // { name: 'ATMs', image: 'assets/svg/black/atm.svg', link: 'AtmsPage' },
      // { name: this.langs.contact_us, image: 'assets/svg/black/contact.svg', link: 'ContactPage' },
      // { name: this.langs.notification, image: 'assets/svg/black/notification.svg', link: 'NotificationPage' }
    ];
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SettingsPage');
  }
  goHome(){
    this.navCtrl.setRoot('HomePage');
  }
  openPage( ppage ) {
    if ( ppage == 'lang' ) {
      this.showConfirm();
    } else {
      this.navCtrl.push( ppage );
    }
  }

  showConfirm() {
    const confirm = this.alertCtrl.create( {
      title: this.langs.language,
      subTitle: this.langs.preferred_lang_txt,
      cssClass: 'popa',
      buttons: [
        {
          text: 'ENGLISH',
          handler: () => {
            this.globalVars.setLang( 'en' );
            this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
            this.load();
          }
        },
        {
          text: 'KISWAHILI',
          handler: () => {
            this.globalVars.setLang( 'ki' );
            this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
            this.load();
          }
        }
      ]
    } );
    confirm.present();
  }
  load() { 
     this.quick_list = [
      { name: this.langs.cheque, image: 'assets/svg/card.svg', link: 'ChequesPage' },
      { name: this.langs.change_pin_txt, image: 'assets/svg/secure.svg', link: 'ChangePinPage' },
      { name: this.langs.enquiries, image: 'assets/svg/enquire.svg', link: 'EnquiriesPage' },
      { name: this.langs.language, image: 'assets/svg/language.svg', link: 'lang' },
      { name: this.langs.forex, image: 'assets/svg/black/forex.svg', link: 'ForexPage' }
    ];
  }
  switch(){
    this.isChange=true;
  }
  ionViewDidEnter() {
    this.globalVars.reload();

  }
  changelang() {
    if (this.langus == 'ki') {
      this.globalVars.setLang( 'ki' );
      this.navCtrl.setRoot('HomePage');
    }
    else if (this.langus == 'en') {
      this.globalVars.setLang( 'en' );
      this.navCtrl.setRoot('HomePage');
    }
  }
}



